
<template>
  <div class="picturemyung"> <!-- body와 html 태그를 제외하고 전체 영역 color 조정을 위해 생성한 div 영역 입니다.  주로 color와 font를 다룹니다.-->

            <h4>Festival Horizontal Flip</h4> <!-- 1,2,3번 카드는 마우스를 올리면 가로를 축으로 회전합니다. -->
            <!-----------------------------------------------1번째 카드------------------------------------------------------------------------>
            <div class="flip"> <!-- 1번째 카드 앞면 -->
                <div class="front" style="background-image: url(https://img.hankyung.com/photo/201906/20190611210901_5cff99dd233cb_1.jpg)">
                  <h1 class="text-shadow"> Concert Name </h1>
                </div>
                <div class="back"> <!-- 1번째 카드 뒷면 -->
                  <h2>싸이의 흠뻑쇼</h2>
                  <p>- 공연주체 : 싸이<br>- 축제이름 : 흠뻑쇼<br>- 기타 축제에 관한 설명</p>
                </div>
            </div>
            <!-----------------------------------------------2번째 카드------------------------------------------------------------------------>
            <div class="flip"> <!-- 2번째 카드 앞면 -->
                <div class="front" style="background-image: url(https://sslimage.interpark.com/ticketimage/Play/ITM/Data/Modify/2017/8/2017081717120154.jpg)">
                  <h1 class="text-shadow"> How Come </h1>
                </div>
                <div class="back"> <!-- 2번째 카드 뒷면 -->
                  <h2>흠뻑쇼 오는길</h2>
                  <p>- 장소 : 흠뻑쇼 장소<br>- 셔틀버스 운행 : 셔틀버스 정보<br>기타 약도</p>
                </div>
            </div>
            <!-----------------------------------------------3번째 카드------------------------------------------------------------------------>
            <div class="flip"> <!-- 3번째 카드 앞면 -->
                <div class="front" style="background-image: url(https://blog.kakaocdn.net/dn/bhAMpJ/btqu13Aeqkj/TYwoMbTukFKiuEQummLG61/img.jpg)">
                  <h1 class="text-shadow"> Seat Arrange </h1>
                </div>
                <div class="back"> <!-- 3번째 카드 뒷면 -->
                  <h2>콘서트 내부</h2>
                  <p>- 내부 : 좌석 배치도<br>- 내부 구조 : 콘서트 내부<br>기타 설명</p>
                </div>
            </div>
            <!-----------------------------------------------한줄 끝------------------------------------------------------------------------>
            <br>
            <br>
            <h4>Festival Vertical Flip</h4> <!-- 4,5,6번 카드는 마우스를 올리면 세로를 축으로 회전합니다. -->
          <!-----------------------------------------------4번째 카드------------------------------------------------------------------------>
            <div class="flip flip-vertical"> <!-- 4번째 카드 앞면 -->
                <div class="front" style="background-image: url(https://www.dailysecu.com/news/photo/201905/51354_43496_0346.jpg)">
                  <h1 class="text-shadow"> Photo Zone </h1>
                </div>
                <div class="back"> <!-- 4번째 카드 뒷면 -->
                  <h2>공연장 포토존</h2>
                  <p>- 내부 사진 촬영 장소<br>- 기타 사진촬영<br>기타 설명</p>
                </div>
            </div>
            <!-----------------------------------------------5번째 카드------------------------------------------------------------------------>
            <div class="flip flip-vertical"> <!-- 5번째 카드 앞면 -->
                <div class="front" style="background-image: url(http://www.dailygrid.net/news/photo/201906/239492_138677_257.png)">
                  <h1 class="text-shadow"> Ticket </h1>
                </div>
                <div class="back"> <!-- 5번째 카드 뒷면 -->
                  <h2>티켓 예매</h2>
                  <p>- 티켓 예매 방법<br>- 온/오프라인 예매 방식 차이<br>기타 설명</p>
                </div>
            </div>
            <!-----------------------------------------------6번째 카드------------------------------------------------------------------------>
            <div class="flip flip-vertical"> <!-- 6번째 카드 앞면 -->
                <div class="front" style="background-image: url(data:image/jpeg;base64,)">
                <h1 class="text-shadow"> Review </h1>
              </div>
                <div class="back"> <!-- 6번째 카드 뒷면 -->
                  <h2>기타 정보 / 후기</h2>
                  <p>- 사용자 후기<br>- 사진<br>기타 설명</p>
                </div>
            </div>
            <!-----------------------------------------------모든 카드 끝------------------------------------------------------------------------>

            <div> <!-- 게시판으로 넘어가는 버튼 -->
              <b-button @click="button('/board')">게시판</b-button>
            </div>

  </div> <!-- body와 html 태그를 제외하고 전체 영역 color 조정을 위해 생성한 div 끝 -->
</template>

<script>
export default {
  methods: {
    button (root) { // 버튼을 누르면 실행되는 메소드로 지정된 페이지로 넘어가도록 router를 사용합니다.
      this.$router.push(root)
      this.$router.go()
    }
  },
  beforeCreate () {
  // vue에서는 body와 html 태그에 접근이 어려워 JavaScript를 이용해 접근했습니다. 페이지가 완전히 생성되기 전에 배경색에 접근해 색을 지정해줍니다.
  // 각 페이지 마다 설정해 페이지마다 다른 색상을 가져올 수 있도록 했습니다.
    document.querySelector('body').style.backgroundColor = '#1f1f1f'
  }
}
</script>

<style scoped>
  @import "../resource/PicturePage/picture_style1.css";
  /* css 입니다. */
</style>



